import React from "react";
import { Row, Col, Card, Image, Pagination, Button } from "antd";
import { ShoppingCartOutlined } from "@ant-design/icons";
import { useDispatch, useSelector } from "react-redux";
import { shopActions } from "@actions";

const initItemShop = [

  {
    src: "data:image/jpeg;base64,",
    title: "title",
    name: "samsung",
    price: "15",
    total: "15",
    quantity: 1,
    description: "des",
  },
  {
    src: "data:image/jpeg;base64,",
    title: "title",
    name: "apple",
    price: "15",
    total: "15",
    quantity: 1,
    description: "des",
  },
  {
    src: "data:image/jpeg;base64,",
    name: "acer",
    price: "15",
    total: "15",
    quantity: 1,
    description: "des",
  },
  {
    src: "https://cdn.tgdd.vn/Products/Images/44/247312/acer-nitro-5-gaming-an515-57-5831-i5-nhqdgsv003-600x600.jpg",
    name: "acer",
    price: "15",
    total: "15",
    quantity: 1,
    description: "des",
  },
];

const ShopContent = () => {
  // const onSearch = value => console.log(value);
  const dispatch = useDispatch();
    const { isAddItemSuccess } = useSelector(state => state.shopReducer);
    
  const { Meta } = Card;
  const handleOnClickAddToCart = (item) => {
    //dispatch action
    const temp = item;
    temp.isAddItemSuccess = !isAddItemSuccess;
    // console.log(shopActions.addItemToCart(temp));
    dispatch(shopActions.addItemToCart(temp));
  };
  return (
    <Col span={16}>
      <Row>
        {initItemShop?.map((item) => (
          <Col className="product__img__border" span={12}>
            <Card
              hoverable
              cover={<Image width={200} alt="example" src={item.src} />}
            >
              <Meta title={item.name} />
            </Card>
            <Button
              className="Add__to__cart "
              with={50}
              type="Add To Cart"
              shape="circle"
              onClick={() => handleOnClickAddToCart(item)}
              icon={<ShoppingCartOutlined />}
            >
              Add To Cart
            </Button>
          </Col>
        ))}
       
      </Row>
      <Pagination size="small" total={50} />
    </Col>
  );
};

export default ShopContent;
